p {
 font-size: 45px;
 margin: 0;
 color: white;
 font-weight: bold;
}

.bg-image {
 position: relative;
 padding-top: 100px;
}

.bg-image::before {
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 content: '';
 background-color: rgba(0, 0, 0, 0.6);
}

.card {
 border-radius: 0;
 width: 100%;
}

.button {
 max-width: 200px;
 color: white;
 border-color: white;
}

.button:hover {
 color: white;
 text-decoration: underline;
}
